.scroll-elevated {
  scrollbar-width: thin;
  scrollbar-color: rgba(56, 189, 248, 0.35) rgba(15, 23, 42, 0.05);
}
.scroll-elevated::-webkit-scrollbar {
  width: 8px;
}
.scroll-elevated::-webkit-scrollbar-track {
  background: linear-gradient(180deg, rgba(148, 163, 184, 0.15), rgba(15, 23, 42, 0.05));
  border-radius: 9999px;
}
.scroll-elevated::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgba(56, 189, 248, 0.6), rgba(59, 130, 246, 0.45));
  border-radius: 9999px;
  box-shadow: 0 0 0 2px rgba(15, 23, 42, 0.15);
}
.scroll-elevated::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, rgba(14, 165, 233, 0.8), rgba(59, 130, 246, 0.65));
}
@keyframes translate-press-bounce {
  0% {
    transform: translateY(0) scale(1);
    box-shadow: 0 10px 20px rgba(37, 99, 235, 0.25);
  }
  55% {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0 16px 26px rgba(56, 189, 248, 0.35);
  }
  100% {
    transform: translateY(0) scale(1.02);
    box-shadow: 0 12px 22px rgba(37, 99, 235, 0.3);
  }
}
@keyframes translate-release-glow {
  0% {
    transform: translateY(0) scale(1.02);
    box-shadow: 0 12px 22px rgba(37, 99, 235, 0.3);
  }
  40% {
    transform: translateY(2px) scale(0.98);
    box-shadow: 0 6px 14px rgba(56, 189, 248, 0.25);
  }
  100% {
    transform: translateY(0) scale(1);
    box-shadow: 0 10px 18px rgba(37, 99, 235, 0.22);
  }
}
.translate-press-animate {
  animation: translate-press-bounce 0.35s ease-out forwards;
}
.translate-release-animate {
  animation: translate-release-glow 0.45s ease-out forwards;
